import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',			// 项目启动页
      redirect: 'bookDetail',			// 重定向到下方声明的路由 
    },  
    {
      path: '/home',
      name: 'home',
      component: resolve => require(['@/pages/home'], resolve)
    },
    {
      path: '/bookDetail',
      name: 'bookDetail',
      component: resolve => require(['@/pages/bookDetail'], resolve)
    },    
    {
      path: '/HotLabel',
      name: 'HotLabel',
      component: resolve => require(['@/pages/HotLabel'], resolve)
    },       
  ]
})





